import Vue from 'vue';
import VueRouter from 'vue-router';

import Home from 'components/Home';
import ShowMusicList from 'components/ShowMusicList';
import ShowMusicDetail from 'components/ShowMusicDetail';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        redirect: '/home'
    },
    {
        path: '/home',
        component: Home
    },
    {
        path: '/home/musiclist',
        component: ShowMusicList
    },
    {
        path: '/home/musicdetail',
        component: ShowMusicDetail
    }
]

const router = new VueRouter({
    routes,
    mode: 'history'
});

export default router;