<template>
    <div class="home">
        <div class="homeAlbum" v-for="item in images" :key="item.url">
          <img :src="item.url" alt="album" @click="search(item.searchStr)">
          <p @click="search(item.searchStr)">{{item.searchStr}}</p>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { MUSICLIST } from '../store/mutationsTypes';

export default {
  name: 'Home',
  data() {
    return {
      images: [
        {
          searchStr: '传说之下 游戏原声带',
          url: "http://p4.music.126.net/oZe-JSuWYAR8zSa29g9g0g==/3254554423361647.jpg?param=200y200"
        },
        {
          searchStr: '易烊千玺',
          url: "http://p2.music.126.net/1gcQxwKhOLAcE_aTFjIz6Q==/18874216602661738.jpg?param=200y200"
        },
        {
          searchStr: 'Shirfine',
          url: "http://p2.music.126.net/UcX3ZSde2-lEKjQ1_sz-tQ==/109951164400602756.jpg?param=200y200"
        },
        {
          searchStr: '明日方舟OST1',
          url: "http://p2.music.126.net/2Es2-eB2nJMGPolpE_Yq5A==/109951164146313613.jpg?param=200y200"
        },
        {
          searchStr: 'The Biggest Dreamer',
          url: "http://p1.music.126.net/I7osbyAjcyrLnNrXytQk-Q==/579442627850015.jpg?param=200y200"
        },
        {
          searchStr: '向井太一',
          url: "http://p1.music.126.net/cnuto10oQO2pf9FZhRtEMw==/109951164343546557.jpg?param=200y200"
        },
        {
          searchStr: 'Gymnopedies',
          url: "http://p3.music.126.net/ZLK4WUKox_SPg8OxR9aD9A==/109951163241486994.jpg?param=200y200"
        },
        {
          searchStr: '小夜曲',
          url: "http://p4.music.126.net/v56HDolDRWNoNhZtc-6Qag==/109951163947939918.jpg?param=200y200"
        }
      ]
    }
  },
  methods: {
    search(str) {
      axios.get("https://autumnfish.cn/search?keywords=" + str)
        .then((res) => {
          this.$store.commit(MUSICLIST, res.data.result.songs);
          console.log(res.data.result.songs);
          // this.$router.push('/musiclist');
          this.$router.push('/home/musiclist');
        }, (err) => {
          console.log(err);
        });
    }
  }
}
</script>

<style scoped>
  @import '../assets/css/home.css';
</style>
